@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
:root {
    --main-red: rgb(179, 32, 21);
}

.container: {
    /* display: flex; */
    /* justifyContent: flex-start; */
    /* flexWrap: wrap */
    postition: -webkit-sticky;
    postition: sticky;
    top: 0;
}

.redLine: {
    display: block;
    postition: -webkit-sticky;
    postition: sticky;
    top: 0;
    background-color: rgb(179, 32, 21);
    background-color: var(--main-red);
    height: 10vh;
    width: 100vw;
}

.scrollBox: {
    display: flex;
    flex: 1 1;
    height: 100vh;
}

.nav: {
    display: flex;
    postition: -webkit-sticky;
    postition: sticky;
    flex-direction: column;
    width: 20vw;
    padding-top: 4vh;
}

.nav > h1: {
    font-family: Bebas Neue;
    font-size: 4vw;
}

.first: {
    margin: 1.5vh 0 0 2vw
}

.last: {
    margin: .2vh 0 0 5vw
}

.button: {
    flex: .05 1;
    font-size: 1.5vw;
    margin: 1vh 2vw 0 2vw;
    text-align: left;
    border: none;
    background-color: rgba(0, 0, 0, 0)
}

.buttonActive: {
    flex: .05 1;
    font-size: 1.5vw;
    margin: 1vh 2vw 0 2vw;
    text-align: left;
    border: none;
    border-left: .2vw solid red;
    padding-left: 1.5vw;
    background-color: rgba(0, 0, 0, 0);
}

/* reset */
#fs-frm input,
#fs-frm select,
#fs-frm textarea,
#fs-frm fieldset,
#fs-frm optgroup,
#fs-frm label,
#fs-frm #card-element:disabled {
  font-family: inherit;
  font-size: 100%;
  color: inherit;
  border: none;
  border-radius: 0;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
}
#fs-frm ::-webkit-input-placeholder {
  font-size: .825em;
  margin-bottom: .5em;
  padding-top: .2em;
  display: flex;
  align-items: baseline;
}
#fs-frm :-ms-input-placeholder {
  font-size: .825em;
  margin-bottom: .5em;
  padding-top: .2em;
  display: flex;
  align-items: baseline;
}
#fs-frm ::-ms-input-placeholder {
  font-size: .825em;
  margin-bottom: .5em;
  padding-top: .2em;
  display: flex;
  align-items: baseline;
}
#fs-frm label,
#fs-frm legend,
#fs-frm ::placeholder {
  font-size: .825em;
  margin-bottom: .5em;
  padding-top: .2em;
  display: flex;
  align-items: baseline;
}

/* border, padding, margin, width */
#fs-frm input,
#fs-frm select,
#fs-frm textarea,
#fs-frm #card-element {
  border: 1px solid rgba(0,0,0,0.2);
  background-color: rgba(255,255,255,0.9);
  padding: .75em 1em;
  margin-bottom: 1.5em;
}
#fs-frm input:focus,
#fs-frm select:focus,
#fs-frm textarea:focus {
  background-color: white;
  outline-style: solid;
  outline-width: thin;
  outline-color: gray;
  outline-offset: -1px;
}
#fs-frm [type="text"],
#fs-frm [type="email"] {
  width: 100%;
}
#fs-frm [type="button"],
#fs-frm [type="submit"],
#fs-frm [type="reset"] {
  width: auto;
  cursor: pointer;
  -webkit-appearance: button;
  -moz-appearance: button;
  appearance: button;
}
#fs-frm [type="button"]:focus,
#fs-frm [type="submit"]:focus,
#fs-frm [type="reset"]:focus {
  outline: none;
}
#fs-frm [type="submit"],
#fs-frm [type="reset"] {
  margin-bottom: 0;
}
#fs-frm select {
  text-transform: none;
}

#fs-frm [type="checkbox"] {
  -webkit-appearance: checkbox;
  -moz-appearance: checkbox;
  appearance: checkbox;
  display: inline-block;
  width: auto;
  margin: 0 .5em 0 0 !important;
}

#fs-frm [type="radio"] {
  -webkit-appearance: radio;
  -moz-appearance: radio;
  appearance: radio;
}

/* address, locale */
#fs-frm fieldset.locale input[name="city"],
#fs-frm fieldset.locale select[name="state"],
#fs-frm fieldset.locale input[name="postal-code"] {
  display: inline;
}
#fs-frm fieldset.locale input[name="city"] {
  width: 52%;
}
#fs-frm fieldset.locale select[name="state"],
#fs-frm fieldset.locale input[name="postal-code"] {
  width: 20%;
}
#fs-frm fieldset.locale input[name="city"],
#fs-frm fieldset.locale select[name="state"] {
  margin-right: 3%;
}

